import { Link } from "gatsby"
import React from "react"
import styled from "styled-components"
import theme from "../styles/theme"

const StyledLink = styled.span`
  cursor: pointer;
  font-family: Roboto-Bold;
  font-size: ${theme.fontSizes.s12};
  line-height: 1;

  color: ${theme.colors.darkBlue};
  padding: 5px 10px;

  box-sizing: border-box;
  border-top: 2px solid ${theme.colors.paleYellow};
  border-bottom: 2px solid ${theme.colors.paleYellow};

  &:hover {
    border-bottom: 2px solid ${theme.colors.orange};
  }
`

const LinkGroup = styled.div`
  display: flex;
`

const StickyHeader = styled.header`
  position: ${props => (props.isMobile ? "absolute" : "fixed")};
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;

  display: flex;
  justify-content: space-between;
  align-items: center;

  height: 80px;
  padding: 0 40px 0;
  background-color: ${theme.colors.paleYellow};
  box-shadow: 0 3px 2px rgba(0, 0, 0, 0.25);
`

