import React from "react"
import { isMobile } from "../media-queries"
import { GitHub, Gmail, LinkedIn, StackOverflow, WhiteReact } from "./elements"
import {
  Bold,
  Copyright,
  IconSet,
  StyledFooter,
  Text,
  TextSet,
} from "./footer.styles"

function FooterRegular() {
  return (
    <StyledFooter>
      <IconSet>
        <LinkedIn href="https://www.linkedin.com/in/rsolopatych/" />
        <GitHub href="https://github.com/rostsolo" />
        <StackOverflow href="https://stackoverflow.com/users/story/7320665" />
        <Gmail />
      </IconSet>
      <TextSet>
        <Text>
          This website is fully designed and implemented by{" "}
          <Bold>Rostyslav Solopatych</Bold>
        </Text>
        <Copyright>
          <Text style={{ paddingRight: "10px" }}>
            <Bold>Powered by</Bold>
          </Text>
          <WhiteReact />
          <Text style={{ paddingLeft: "40px" }}>
            © Copyright {new Date().getFullYear()}
          </Text>
        </Copyright>
      </TextSet>
    </StyledFooter>
  )
}

function FooterMobile() {
  return (
    <StyledFooter>
      <TextSet>
        <Text>
          This website is fully designed and implemented by{" "}
          <Bold>Rostyslav Solopatych</Bold>
        </Text>
      </TextSet>
      <IconSet style={{ paddingTop: "20px", justifyContent: "flex-end" }}>
        <LinkedIn href="https://www.linkedin.com/in/rsolopatych/" />
        <GitHub href="https://github.com/rostsolo" />
        <StackOverflow href="https://stackoverflow.com/users/story/7320665" />
        <Gmail />
      </IconSet>
      <TextSet style={{ justifyContent: "flex-end" }}>
        <Copyright>
          <Text>
            <Bold>Powered by</Bold>
          </Text>
          <WhiteReact />
          <Text>© Copyright {new Date().getFullYear()}</Text>
        </Copyright>
      </TextSet>
    </StyledFooter>
  )
}

const Footer = isMobile ? FooterMobile : FooterRegular

export default Footer
