import styled from "styled-components"
import theme from "./../styles/theme"
import { device, isMobile } from "../media-queries"

const Copyright = styled.div`
  display: flex;
  padding-top: 20px;
`

const Bold = styled.span`
  font-family: Roboto-Bold;
  font-size: ${theme.fontSizes.s12};
`

const Text = styled.div`
  font-family: Roboto;
  font-size: ${theme.fontSizes.s12};
  letter-spacing: 0.4px;
  text-align: right;
`

const TextSet = styled.div`
  display: flex;
  flex: 1;
  align-items: flex-end;

  @media ${device.tablet} {
    flex-direction: column;
    justify-content: center;
  }
`

const IconSet = styled.div`
  display: flex;
  flex: 1;
  align-items: center;
`

const Footer = styled.footer`
  display: flex;
  justify-content: space-between;
  padding: 0 80px;
  min-height: 150px;
  background-color: ${theme.colors.black};
  color: ${theme.colors.white};
`

const FooterMobile = styled.footer`
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: ${theme.colors.black};
  color: ${theme.colors.white};
`

const StyledFooter = isMobile ? FooterMobile : Footer

export { Copyright, Bold, Text, TextSet, IconSet, StyledFooter }
