import React from "react"
import styled from "styled-components"
import Header from "./header"
import Footer from "./footer"

const Layout = ({ children }) => {
  return (
    <Body>
      <Header />
      <main>{children}</main>
      <Footer />
    </Body>
  )
}

const Body = styled.div`
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: auto;
  min-height: 100vh;
`

export default Layout
