import React from "react"
import { isMobile } from "./../media-queries"
import { StyledLink, LinkGroup, StickyHeader, LogoIcon } from "./header.styles"
import useScrollTo from "../hooks/scrolling.hook"

const Header = () => {
  const scrollToAbout = useScrollTo("about")
  const scrollToContact = useScrollTo("contact")

  return (
    <StickyHeader isMobile={isMobile}>
      <LogoIcon />
      {!isMobile && (
        <LinkGroup>
          <StyledLink onClick={scrollToAbout}>ABOUT ME</StyledLink>
          <StyledLink onClick={scrollToContact}>CONTACT ME</StyledLink>
          <StyledLink>DOWNLOAD MY CV</StyledLink>
        </LinkGroup>
      )}
    </StickyHeader>
  )
}

export default Header
