import React from "react"
import styled from "styled-components"
import { device, isMobile } from "../media-queries"
import theme from "../styles/theme"

export const RightCircle = styled.div`
  position: absolute;
  border-radius: 50%;
  background-color: ${theme.colors.orange};

  @media ${device.mobileS} {
    top: -20px;
    right: -20px;
    height: 200px;
    width: 200px;
  }
  @media ${device.tablet} {
    top: -20px;
    right: -20px;
    height: 200px;
    width: 200px;
  }
  @media ${device.laptop} {
    top: -60px;
    right: -100px;
    height: 500px;
    width: 500px;
  }
`

export const BottomCircle = styled.div`
  position: absolute;
  border-radius: 50%;
  background-color: ${theme.colors.orange};

  @media ${device.mobileS} {
    bottom: -10vh;
    left: -15vw;
    width: 120vw;
    height: 20vh;
  }
  @media ${device.tablet} {
    top: 480px;
    left: -15vw;
    width: 120vw;
    height: 706px;
  }
`

export const YellowCircle = styled.div`
  position: absolute;
  border-radius: 50%;
  background-color: ${theme.colors.gold};
  opacity: 0.5;

  @media ${device.laptop} {
    top: 42px;
    right: 350px;
    width: 150px;
    height: 150px;
  }
`

