const Anchor = ({ href, children }) => {
  const StyledAnchor = styled.a`
    margin-right: 40px;
    cursor: pointer;
    &:hover {
      & path:not(.always-white) {
        fill: ${theme.colors.orange}; //#61dafb;
      }
    }
  `
  return (
    <StyledAnchor href={href} target="_blank" rel="noopener noreferrer">
      {children}
    </StyledAnchor>
  )
}

